@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --grad-1: rgb(81,70,193);
  --grad-2: rgb(188,139,93);
}

html, body, #root {
  height:100%;
  min-height:100%; 
  
}

body {
  color: #d1d1d1;
  background: #1d1d20;
  font-family: "Montserrat", sans-serif;
}

@keyframes gradient {
	0% {
		background-size: 500% 500%; 
    background-position: 100% 100%;
	}
	100% {
		background-size: 100% 100%;
    background-position: 0% 0%;
	}
}

.lightmode {
  background: radial-gradient(at bottom left,rgba(74,222,128,1) 0%,rgba(59,130,246,1) 50%, rgb(22, 63, 129) 100%);
  
}

.darkmode {
  background: radial-gradient(at bottom left,rgb(32, 124, 66) 0%,rgb(33, 73, 136) 50%, rgb(5, 29, 66) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.gradient1 {
  stop-color: var(--grad-1);
  transition: all .3s ease-in-out;
}

.gradient2 {
  stop-color: var(--grad-2);
  transition: all .3s ease-in-out;
}

.main {
  box-sizing: border-box;
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;

  height:100%;
  min-height:100%; 
  background-size: 500% 500%;
  animation-name: gradient;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-fill-mode: forwards;
}

.container {
  max-width: 100vw;
  height: 100%;
  box-sizing: border-box;
  justify-content: center;
  align-items: flex-start;
  display: flex
}

@media screen and (min-width: 900px) {
  .container {
      height:70vh;
      max-height: 100vh;
      align-items: center;
      overflow: hidden
  }
}

.title {
  opacity: 0;
  animation: fadeIn 1s 1s;
  animation-fill-mode: forwards;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}
